
 // Variables 
 $red: #ad0000;
 
// Placeholder selectors 
 %transition {
   transition: all .25s;
 }
 
 /*=Typography=*/
 
 html {
   font-size: 16px;
 }
 
 body {
   font-family: 'Open sans', sans-serif;
   font-weight: 300;
 }
 
 .navbar a,
 .sec-about p {
   text-align: center;
 }
 
 .navbar a {
   font-size: 16px;
 }
 
 .navbar .fa-external-link {
   font-size: 10px;
 }
 
 .main-heading {
   font-family: 'Open sans', sans-serif;
   font-weight: 300;
   text-align: center;
   letter-spacing: 9px;
   text-transform: uppercase;
 }
 
 .container > h1 {
   font-weight: 300;
   text-align: center;
   text-transform: uppercase;
 }
 
 p {
   font-size: 16px;
   line-height: 1.5;
 }
 
 .sec-about a {
   text-decoration: underline;
 }
 
 .sec-portfolio h2,
 .sec-portfolio p {
   font-weight: 300;
   text-align: center;
 }
 .img-fluid img-thumbnail {
   width: 200px;
   height: 200px
 }
 
 .sec-testimonials p {
   font-style: italic;
 }
 
 .sec-contact .btn {
   text-transform: uppercase;
 }
 
 .soc-media-ul a {
   text-align: center;
   text-decoration: none;
 }
 
 /*=Base=*/
 
 section {
   padding: 5em 0;
 }
 
 footer {
   padding: 3em 0;
 }
 
 form {
   @media screen and (max-width: 768px) and (min-width: 485px) {
     max-width: 70%;
   }
 }
 
 ul {
   padding: 0;
   margin: 0;
   list-style-type: none;
 }
 
 a {
   @extend %transition;
 }
 
 hr {
   margin-bottom: 3em;
   width: 7%;
 }
 
 /*=Layout=*/
 
 .sec-folio .row {
   margin-bottom: 3em;
 }
 
 .sec-folio img {
   max-width: 100%;
 }
 
 /*Header*/
 
 .navbar-default {
   background: #fff;
 }
 
 .navbar .fa-external-link {
   vertical-align: top;
 }
 
 .navbar-default .navbar-nav>li>a:hover,
 .navbar-default .navbar-nav>li>a:focus {
   color: $red;
 }
 
 .navbar-default .navbar-nav>li>a:active {
   color: darken($red, 5%);
 }
 
 /*Main*/
 
 .sec-main {
   position: relative;
   height: 560px;
   background: url("./images/abbeyroad.jpg") no-repeat center center;
   background-size: cover;
 
   &:after {
     content: "";
     position: absolute;
     top: 0;
     left: 0;
     width: 100%;
     height: 100%;
     background: rgba(0, 0, 0, .5);
   }
 }
 
 .main-heading {
   position: absolute;
   top: 0;
   right: 0;
   bottom: 0;
   left: 0;
   z-index: 2;
   padding: 0;
   margin: auto;
   height: 40px;
   color: #fff;
 }
 
 /*About*/
 
 .sec-about a {
   color: #000;
   &:hover,
   &:focus {
     color: $red;
   }

   
 }
 
 /*Portfolio*/
 
 .sec-portfolio {
   background: #f9f9f9;
 }
 
 .sec-portfolio i {
   display: block;
   margin: auto;
   width: 45px;
 }
 

 
 .sec-services h2 {
   margin: 1em 0;
 }

ol li {
  display: list-item;  
  padding: 5px 10px;
  color:white;
  margin: 0 3px;
  text-align: center;
}
 
 
 /*Contact*/
 
 .sec-contact .form-control:focus {
   border-color: $red;
   box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(173, 0, 0, .6);
 }
 
 .sec-contact .btn {
   padding: .5rem 1.75rem;
   background: #000;
   font-size: .95rem;
   color: #fff;
   
   @extend %transition;
   
   &:hover,
   &:focus {
     background: $red;
     border-color: darken($red, 10%);
   }
 }
 
 /*Footer*/
 
 footer {
   background: whitesmoke;
 }
 
 .soc-media-ul {
   display: flex;
   justify-content: center;
 }
 
 .soc-media-ul li:not(:first-child) a {
   margin-left: 1em;
 }
 
 .soc-media-ul a {
   display: block;
   padding: 0.5em .65rem;
   font-size: 1rem;
   color: #fff;
   border-radius: 50%;
   border: 2px solid;
 
   &:hover,
   &:focus {
     color: $red;
   }
 }
 
 .img-fluid {
   width: 350px;
   height: 350px;
 }
 .about-img {
   width: 200px;
   height: 200px;
   margin-left: 20px;
 }

 .links {
   text-align: center;
 }